import * as React from 'react'
import { Helmet } from 'react-helmet'
import Navbar from './Navbar'
import './layout.css';

interface LayoutProps extends React.HTMLProps<HTMLDivElement> {
  children: any
}

export default class extends React.Component<LayoutProps, {}> {
  constructor(props: LayoutProps, context: any) {
    super(props, context)
  }

  componentDidMount () {
    const scriptDatas = [ 
      {
        src: 'https://code.jquery.com/jquery-3.5.1.slim.min.js',
        integrity: 'sha384-DfXdz2htPH0lsSSs5nCTpuj/zy4C+OGpamoFVy38MVBnE+IbbVYUew+OrCXaRkfj',
        crossorigin: 'anonymous'
      },
      {
        src: 'https://cdn.jsdelivr.net/npm/popper.js@1.16.0/dist/umd/popper.min.js',
        integrity: 'sha384-Q6E9RHvbIyZFJoft+2mJbHaEWldlvI9IOYy5n3zV9zzTtmI3UksdQRVvoxMfooAo',
        crossorigin: 'anonymous'
      },
      {
        src: 'https://stackpath.bootstrapcdn.com/bootstrap/4.5.0/js/bootstrap.min.js',
        integrity: 'sha384-OgVRvuATP1z7JjHLkuOU7Xw704+h835Lr+6QL9UvYjZE3Ipu6Tp75j7Bh/kR0JKI',
        crossorigin: 'anonymous'
      }
    ];

    const deferLoad = () => {
      if (scriptDatas.length === 0) return;

      const scriptData: any = scriptDatas.shift();
      const scriptTag: any = document.createElement('script');

      scriptTag.addEventListener('load', deferLoad);

      document.body.appendChild(scriptTag);

      Object.keys(scriptData).forEach((attr: string): any => {
        const value: string = scriptData[attr];
        scriptTag[attr] = value;
      });
    };

    deferLoad();
  }

  public render() {
    return (
      <>
        <Helmet>
          <link
            rel='stylesheet'
            href='https://stackpath.bootstrapcdn.com/bootstrap/4.5.0/css/bootstrap.min.css'
            integrity='sha384-9aIt2nRpC12Uk9gS9baDl411NQApFmC26EwAOH8WgZl5MYYxFfc+NcPb1dKGj7Sk'
            crossOrigin='anonymous'
          />
        </Helmet>
        <div>
          <Navbar />
          { this.props.children }
        </div>
      </>
    )
  }
}
