import * as React from 'react'
import { PageProps } from 'gatsby'
import { navbar as navbarData } from '../../content/intl/ja.json'
import { animateScroll as scroll, scroller } from 'react-scroll'
import SvgLogo from './SvgLogo'

interface NavbarProps extends React.HTMLProps<HTMLDivElement> {
}

export default class extends React.Component<NavbarProps, any> {
  constructor (props) {
    super(props)

    this.state = {
      duration: 500,
      navbarHeight: 56, // @TODO 動的に取得したかったがうまくいかなかったため決め打ち
      shouldChangeNavbarColor: false
    }

    this.updateNavbarColor = this.updateNavbarColor.bind(this)
    this.scrollToTop = this.scrollToTop.bind(this)
  }

  componentDidMount () {
    window.addEventListener('scroll', this.updateNavbarColor)
    this.updateNavbarColor()
  }

  componentWillUnmount () {
    window.removeEventListener('scroll', this.updateNavbarColor)
  }

  updateNavbarColor () {
    const scrollY: number = document.documentElement.scrollTop || window.pageYOffset

    this.setState({
      shouldChangeNavbarColor: window.innerHeight - this.state.navbarHeight - 1 < scrollY
    })
  }

  scrollToTop () {
    scroll.scrollToTop({
      duration: this.state.duration
    })
  }

  scrollTo (element: string) {
    scroller.scrollTo(element, {
      duration: this.state.duration,
      smooth: true,
      offset: this.state.navbarHeight * -1
    })
  }

  public render() {
    return (
      <nav className={`navbar navbar-expand-lg fixed-top ${this.state.shouldChangeNavbarColor ? 'navbar-light navbar--changed' : 'navbar-dark'}`}>
        <div className="container">
          <a className="navbar-brand" onClick={ this.scrollToTop } style={{
            cursor: 'pointer'
          }}>
            <SvgLogo />
          </a>
          
          <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>

          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav mr-auto">
              {navbarData.map((d: any, i: number) => (
                <li className="nav-item" key={i}>
                  <a className="nav-link" onClick={ () => this.scrollTo(d.target) } style={{
                    cursor: 'pointer'
                  }}>{ d.label }</a>
                </li>
              ))}
            </ul>
          </div>

        </div>
      </nav>
    )
  }
}
