import * as React from 'react'
import { PageProps } from 'gatsby'
import { service as serviceData } from '../../content/intl/ja.json'

const images: any[] = [];
images.push(require('../images/service-1.svg'))
images.push(require('../images/service-2.svg'))
images.push(require('../images/service-3.svg'))
images.push(require('../images/service-4.svg'))
images.push(require('../images/service-5.svg'))

interface ServiceProps extends React.HTMLProps<HTMLDivElement> {
}

export default class extends React.Component<ServiceProps, any> {
  constructor (props) {
    super(props)

    this.handleClickMoreButton = this.handleClickMoreButton.bind(this)

    this.state = {
      clickedMoreButton: false
    }
  }

  handleClickMoreButton () {
    this.setState({
      clickedMoreButton: true
    })
  }

  public render() {
    return (
      <section id="service">
        <div className="container">
          <h1>{ serviceData.title }</h1>
          <div className="service-background">
            <div className="row row-cols-1 row-cols-lg-2">
              <div className="col-lg-4">
                <div className="service-item service-item-1">
                  <img src={images[0]} alt={serviceData.items[0].label} />
                  <h2 style={{ color: serviceData.items[0].color}}>{ serviceData.items[0].label }</h2>
                  <p>{ serviceData.items[0].description }</p>
                </div>
              </div>
              <div className={`col-lg-4 ${ this.state.clickedMoreButton ? 'd-none' : 'd-sm-none' }`}>
                <div className="service-morebutton">
                  <button
                    className="btn btn-outline-secondary"
                    onClick={ this.handleClickMoreButton }
                    type="button"
                  >▼続きを読む</button>
                </div>
              </div>
              { serviceData.items.map((d: any, i: number) => {
                // 1番目の要素だけ特殊なためループ外で定義済
                if (i === 0) return
                return (
                  <div
                    key={i}
                    className={ (i === 3 ? 'col-lg-4 offset-lg-2' : 'col-lg-4') + (this.state.clickedMoreButton ? '' : ' d-none d-sm-block') }>
                    <div className={`service-item service-item-${i+1}`}>
                      <img src={images[i]} alt={d.label} />
                      <h2 style={{ color: d.color}}>{ d.label }</h2>
                      <p>{ d.description }</p>
                    </div>
                  </div>
                )
              }) }
            </div>
          </div>
        </div>
      </section>
    )
  }
}
